import {
  Component,
  OnInit,
  Input,
  EventEmitter,
  Output,
  ViewChild,
  QueryList,
  AfterViewInit,
  ViewEncapsulation,
  ChangeDetectionStrategy,
} from "@angular/core";
import { MatSelect } from "@angular/material/select";
import { MatOption } from "@angular/material/core";

@Component({
  selector: "app-multi-select",
  templateUrl: "./multi-select.component.html",
  styleUrls: ["./multi-select.component.scss"],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MultiSelectComponent implements OnInit, AfterViewInit {
  @Input() values: string[];
  changedValues: string[];
  @Input() placeholder: string;
  @Output() isSelected = new EventEmitter<string>();

  @ViewChild("mySelect") skillSel: MatSelect;
  @ViewChild("formField") formField;
  allSelected = false;
  constructor() {}
  ngAfterViewInit(): void {}

  ngOnInit(): void {
    this.changedValues = this.values;
  }

  onChange(event) {
    this.isSelected.emit(event);
  }

  checkAllOptions() {
    this.allSelected = !this.allSelected;

    if (this.allSelected) {
      this.skillSel.options.forEach((item: MatOption) => item.select());
    } else {
      this.skillSel.options.forEach((item: MatOption) => {
        item.deselect();
      });
    }
  }

  filterOptions(event) {
    const text = event.target.value;

    if (text === "") {
      this.changedValues = this.values;
    } else {
      const options = this.values.filter((item) => {
        return (
          text !== "" && item.toLowerCase().indexOf(text.toLowerCase()) != -1
        );
      });

      this.changedValues = options;
    }
  }
}
