import {
  Component,
  ViewEncapsulation,
  OnInit,
  Directive,
  TemplateRef,
  AfterContentChecked,
  ContentChildren,
  QueryList,
  AfterViewInit,
  ViewChild,
  Input,
  ViewChildren,
  AfterContentInit,
  Renderer2,
  Output,
  EventEmitter,
  HostBinding,
} from "@angular/core";

@Component({
  selector: "app-accordion",
  templateUrl: "./accordion.component.html",
  styleUrls: ["./accordion.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class AccordionComponent implements OnInit, AfterContentInit {
  @ContentChildren("atechPanel", { descendants: true }) panels: QueryList<any>;
  @ContentChildren("active") activeds: QueryList<any>;
  @HostBinding('class') @Input('class') classList: string;

  @Output() panelChange: EventEmitter<any> = new EventEmitter<any>();

  activedTitles: string[] = [];
  titles: string[] = [];
  contents: string[] = [];

  constructor(private renderer: Renderer2) {}

  ngAfterContentInit() {
    this.fillTitlesAndContents();
    this.setActivatedTitles();
  }

  fillTitlesAndContents() {
    this.panels.forEach((panel) => {
      this.titles.push(panel.nativeElement.children[0].innerHTML);
      this.contents.push(panel.nativeElement.children[1].innerHTML);
    });
  }

  setActivatedTitles() {
    this.activeds.forEach((actived) => {
      this.activedTitles.push(actived.nativeElement.children[0].innerHTML);
    });
  }

  ngOnInit(): void {}
}
