import {
  Component,
  OnInit,
  ViewContainerRef,
  ViewChild,
  ContentChild,
  ComponentRef,
  ComponentFactoryResolver,
  ComponentFactory,
  EventEmitter,
  Output,
  Input,
  Renderer2,
} from "@angular/core";
import { MatMenu } from "@angular/material/menu";
import { MenuComponent } from "../menu/menu.component";

@Component({
  selector: "app-mainmenu-desktop",
  templateUrl: "./mainmenu-desktop.component.html",
  styleUrls: ["./mainmenu-desktop.component.scss"],
})
export class MainmenuDesktopComponent {
  @ContentChild("atechMenu") itemsList;
  @ViewChild("alertContainer", { read: ViewContainerRef }) container;
  @Output() selected = new EventEmitter<any>();
  @Input() icons: boolean = true;

  items: Array<{ text: string; submenu: Array<Object> }> = [];
  menuItems: Array<{ text: string; elementRef: MatMenu }> = [];
  componentRef: ComponentRef<any>;

  constructor(
    private resolver: ComponentFactoryResolver,
    private renderer: Renderer2
  ) {}

  ngAfterContentInit(): void {
    const mainMenu = this.itemsList.nativeElement;
    this.items = this.menuFill(mainMenu);
  }

  ngAfterViewInit(): void {
    this.createSubmenus();
  }
  ngOnInit(): void {}

  menuFill(menu) {
    const items = menu.children;
    let itemList = [];
    for (let index = 0; index < items.length; index++) {
      let item = { text: "", submenu: null };
      const element = items[index];

      if (element.attributes.submenu) {
        item.text = element.attributes.name.value;
        item.submenu = this.menuFill(element);
      } else item.text = element.innerText;

      itemList.push(item);
    }
    return itemList;
  }

  select(pText: string) {
    this.selected.emit(pText);
  }

  createSubmenus() {
    this.items.forEach((item, index) => {
      let menuItem = { text: "", elementRef: null };

      menuItem.text = item.text;
      if (item.submenu) {
        menuItem.elementRef = this.createComponent(item.submenu, index);
      }
      this.menuItems.push(menuItem);
    });
  }

  createComponent(submenu, index) {
    const factory: ComponentFactory<any> = this.resolver.resolveComponentFactory(
      MenuComponent
    );

    this.componentRef = this.container.createComponent(factory);

    this.componentRef.instance.submenu = submenu;

    this.componentRef.instance.output.subscribe(
      (event) => (this.menuItems[index].elementRef = event)
    );
    this.componentRef.instance.selected.subscribe((pText) =>
      this.selected.emit(pText)
    );
  }
}
