import {
  Component,
  OnInit,
  ContentChild,
  AfterViewInit,
  Input,
  Renderer2,
} from "@angular/core";
import { mainLogo } from "../../assets/imagesInBase64";

@Component({
  selector: "app-navigation-bar-desktop",
  templateUrl: "./navigation-bar-desktop.component.html",
  styleUrls: ["./navigation-bar-desktop.component.scss"],
})
export class NavigationBarDesktopComponent implements OnInit, AfterViewInit {
  mainLogo = mainLogo;
  @ContentChild("buttons") buttonsDiv;
  defaultButtons: Array<{ button: Object; defaultColor: string }> = [];
  @Input() initialPage: number = 1;

  constructor(private renderer: Renderer2) {}

  ngOnInit(): void {}

  ngAfterViewInit(): void {
    this.setDivStyle();
    this.setButtonsStyle();
  }

  setDivStyle() {
    const elementRef = this.buttonsDiv.nativeElement;
    this.renderer.setStyle(elementRef, "display", "flex");
    this.renderer.setStyle(elementRef, "align-items", "center");
  }

  setButtonsStyle() {
    const buttons = this.buttonsDiv.nativeElement.children;
    for (const button of buttons) {
      this.renderer.setStyle(button, "margin-left", "15px");

      if (button.localName === "app-mainmenu-desktop") {
        this.buttonStyle(button.firstChild.firstChild.firstChild);
      } else {
        this.buttonStyle(button);
      }
    }
    const pageIndex = this.initialPage - 1;
    this.setActive(buttons[pageIndex]);
  }

  buttonStyle(button) {
    let defaultColor;
    this.renderer.setStyle(button, "background", "none");
    this.renderer.setStyle(button, "border", "none");
    this.renderer.setStyle(button, "padding", "2px 0");
    this.renderer.setStyle(button, "font-size", "14px");
    this.renderer.setStyle(button, "font-weight", "bold");
    this.renderer.setStyle(button, "outline", "none");
    if (!button.style.color) {
      defaultColor = window
        .getComputedStyle(document.body)
        .getPropertyValue("--primary_lighter_70");
      this.renderer.setStyle(button, "color", `${defaultColor}`);
    } else defaultColor = button.style.color;

    this.defaultButtons.push({ button, defaultColor });
  }

  setActive(button) {
    this.defaultButtons.forEach((defaultButton) => {
      if (button === defaultButton.button) {
        this.renderer.setStyle(button, "border-color", "white");
        this.renderer.setStyle(button, "border-bottom", "1px solid");
        this.renderer.setStyle(button, "color", "white");
      }
    });
  }

  resetDefaultStyle(button) {
    this.defaultButtons.forEach((defaultButton) => {
      if (button !== defaultButton.button) {
        this.renderer.setStyle(defaultButton.button, "border-bottom", "none");

        this.renderer.setStyle(
          defaultButton.button,
          "color",
          defaultButton.defaultColor
        );
      }
    });
  }

  select(evt) {
    this.resetDefaultStyle(evt);
    this.setActive(evt);
  }
}
