import { NestedTreeControl } from "@angular/cdk/tree";
import {
  Component,
  OnInit,
  ElementRef,
  AfterViewInit,
  ContentChildren,
  ContentChild,
  Input,
  Output,
  EventEmitter,
} from "@angular/core";
import { MatTreeNestedDataSource } from "@angular/material/tree";

interface Node {
  name: string;
  children?: Node[];
}

@Component({
  selector: "app-tree-view",
  templateUrl: "./tree-view.component.html",
  styleUrls: ["./tree-view.component.scss"],
})

export class TreeViewComponent implements OnInit, AfterViewInit {
  treeControl = new NestedTreeControl<Node>((node) => node.children);
  dataSource = new MatTreeNestedDataSource<Node>();
  data: Node[] = [];
  @Input() hasIcons: boolean = true;
  @ContentChild("tree") tree;
  @Output() nodeSelected = new EventEmitter<any>();

  constructor() {}
  ngOnInit(): void {}

  ngAfterViewInit(): void {
    this.data = this.fillTreeData(this.tree.nativeElement);
    this.dataSource.data = this.data;
  }

  changeClass(e: any) {
      document.querySelector('.active').classList.remove('active');
      e.srcElement.classList.add('active');
  }

  fillTreeData(tree) {
    const items = tree.children;
    let itemList = [];
    for (let index = 0; index < items.length; index++) {
      let item = { name: "", children: null };
      const element = items[index];

      if (element.attributes.treeNode) {
        item.name = element.attributes.treeNode.value;
        item.children = this.fillTreeData(element);
      } else item.name = element.innerText;

      itemList.push(item);
    }
    return itemList;
  }

  isFirst(node) {
    for (let index = 0; index < this.data.length; index++) {
      const element = this.data[index];
      if (element === node) {
        return true;
      }
    }

    return false;
  }

  hasChild = (_: number, node: Node) =>
    !!node.children && node.children.length > 0;

  emit($event) {
    this.nodeSelected.emit($event);
  }
}
