import {
  Component,
  OnInit,
  HostBinding,
  Output,
  EventEmitter,
  Input,
} from "@angular/core";

@Component({
  selector: "app-strength-password",
  templateUrl: "./strength-password.component.html",
  styleUrls: ["./strength-password.component.scss"],
})
export class StrengthPasswordComponent implements OnInit {
  @Input() value = 20;
  // @HostBinding("class") strength = "";
  @Input() required: boolean;
  @Input() strength: string;
  @Output() blur = new EventEmitter();

  constructor() {}

  ngOnInit(): void {}

  changeInput($event) {
    const strength = this.checkStrength($event.target.value);
    this.changeValues(strength);
  }

  checkStrength(password: string): number {
    var strength = 0;
    if (password.match(/[A-Z]+/) && password.match(/[$@#&!]+/)) {
      strength += 1;
      if (password.length > 8) {
        strength += 1;
      }
    }
    return strength;
  }

  changeValues(strength: number) {
    if (strength === 2) {
      // this.strength = "strong";
      this.value = 100;
    } else if (strength === 1) {
      // this.strength = "good";
      this.value = 50;
    } else {
      // this.strength = "";
      this.value = 25;
    }
  }
}
