import {
  Directive,
  ViewContainerRef,
  ElementRef,
  HostListener,
  ComponentFactoryResolver,
  ComponentFactory,
  ComponentRef,
  OnInit,
  Input,
  Output,
  EventEmitter,
} from "@angular/core";

import { VerticalStepperComponent } from "./vertical-stepper/vertical-stepper.component";

@Directive({
  selector: "[createVerticalStepper]",
})
export class CreateVerticalStepperDirective implements OnInit {
  @Input() verticalSteps: HTMLCollection;
  @Output() sent: EventEmitter<any> = new EventEmitter();
  @Output() isDone: EventEmitter<any> = new EventEmitter();
  componentRef: ComponentRef<any>;
  constructor(
    private el: ElementRef,
    private container: ViewContainerRef,
    private resolver: ComponentFactoryResolver
  ) {}

  ngOnInit(): void {
    this.createComponent();
  }

  createComponent() {
    const factory: ComponentFactory<any> = this.resolver.resolveComponentFactory(
      VerticalStepperComponent
    );

    this.componentRef = this.container.createComponent(factory);

    this.componentRef.instance.verticalStepsCollection = this.verticalSteps;
    this.componentRef.instance.sent.subscribe((step) => this.sent.emit(step));
    this.componentRef.instance.done.subscribe((isDone) =>
      this.isDone.emit(isDone)
    );
  }
}
