import { Component, OnInit, Input, HostBinding } from "@angular/core";
import { DomSanitizer } from "@angular/platform-browser";
import { defaultLogoIcon, oktoLogoIcon, makronLogoIcon } from "../../assets/imagesInBase64";

@Component({
  selector: "app-icon",
  templateUrl: "./icon.component.html",
  styleUrls: ["./icon.component.scss"],
})
export class IconComponent implements OnInit {
  // @Input() src: any;
 @Input() src: any;
  @Input() badge: string;
  @Input() size: string;
  @Input() color: string;
  @Input() background: string;
  @Input() type: string = "";

  constructor(private dom: DomSanitizer) {}

  @HostBinding("class") @Input("class") classList: string = "__icone";

  ngOnInit(): void {
    if (this.src === "atech") {
      this.src = this.dom.bypassSecurityTrustUrl(defaultLogoIcon);
    }
    if (this.src === "okto"){
    this.src = this.dom.bypassSecurityTrustUrl(oktoLogoIcon);
    }
    if (this.src === "makron"){
      this.src = this.dom.bypassSecurityTrustUrl(makronLogoIcon);
      }
    }
}
