import {
  Component,
  OnInit,
  EventEmitter,
  Output,
  ContentChild,
  AfterViewInit,
  Renderer2,
} from "@angular/core";

@Component({
  selector: "app-navigation-bar-mobile",
  templateUrl: "./navigation-bar-mobile.component.html",
  styleUrls: ["./navigation-bar-mobile.component.scss"],
})
export class NavigationBarMobileComponent implements OnInit, AfterViewInit {
  @Output() selected = new EventEmitter<string>();

  constructor(private renderer: Renderer2) {}

  @ContentChild("menu") menu;

  ngOnInit(): void {}
  ngAfterViewInit(): void {
    const button = this.menu.el.nativeElement.children[0].children[0];
    this.renderer.setStyle(button, "background", "transparent");
    this.renderer.setStyle(button, "color", "white");
  }

  change(evt) {
    this.selected.emit(evt);
  }
}
