import {
  Component,
  Input,
  EventEmitter,
  Output,
  ViewChild,
  OnInit,
  AfterViewInit,
  ComponentFactory,
  ComponentFactoryResolver,
  ComponentRef,
  ViewContainerRef,
} from "@angular/core";
import { trigger, style, animate, transition } from "@angular/animations";
import { MatMenu } from "@angular/material/menu";

@Component({
  selector: "menu",
  templateUrl: "./menu.component.html",
  styleUrls: ["./menu.component.scss"],
})
export class MenuComponent implements OnInit, AfterViewInit {
  menuItems: Array<{ text: string; elementRef: MatMenu }> = [];
  @Input() submenu;
  @Output() output = new EventEmitter();
  @Output() selected = new EventEmitter();
  @ViewChild("alertContainer", { read: ViewContainerRef }) container;
  componentRef: ComponentRef<any>;

  @ViewChild("submenu") menu: MatMenu;

  constructor(private resolver: ComponentFactoryResolver) {}

  ngOnInit(): void {}

  ngAfterViewInit(): void {
    this.output.emit(this.menu);
    this.createSubmenus();
  }

  select(pText: string) {
    this.selected.emit(pText);
  }

  createSubmenus() {
    this.submenu.forEach((item, index) => {
      let menuItem = { text: "", elementRef: null };

      menuItem.text = item.text;
      if (item.submenu) {
        menuItem.elementRef = this.createComponent(item.submenu, index);
      }
      this.menuItems.push(menuItem);
    });
  }

  createComponent(submenu, index) {
    const factory: ComponentFactory<any> = this.resolver.resolveComponentFactory(
      MenuComponent
    );
    this.componentRef = this.container.createComponent(factory);
    this.componentRef.instance.submenu = submenu;
    this.componentRef.instance.output.subscribe(
      (event) => (this.menuItems[index].elementRef = event)
    );
    this.componentRef.instance.selected.subscribe((pText) =>
      this.selected.emit(pText)
    );
  }
}
