import {
  Component,
  OnInit,
  ViewChild,
  AfterViewInit,
  Renderer2,
  Input,
  OnChanges,
  SimpleChanges,
} from "@angular/core";
import { trigger } from "@angular/animations";
import {
  emptyLogo,
  logoBorder,
  logoPainted,
} from "../../assets/imagesInBase64";

@Component({
  selector: "app-loading",
  templateUrl: "./loading.component.html",
  styleUrls: ["./loading.component.scss"],
})
export class LoadingComponent implements OnInit, AfterViewInit, OnChanges {
  @Input() height = 40;
  @Input() width = 110;

  @Input() percentage = 50;

  private ctx: CanvasRenderingContext2D;
  @ViewChild("canvas") canvas;
  @ViewChild("backgroundCanvas") backgroundCanvas;

  backgroundEl;
  constructor(private renderer: Renderer2) {}

  ngOnInit(): void {}

  ngAfterViewInit(): void {
    this.loadComponent();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (this.backgroundCanvas) this.loadBackground();
  }

  loadComponent() {
    this.loadBackground();
    this.loadImage();
  }

  loadBackground() {
    const canvasEl: HTMLCanvasElement = this.backgroundCanvas.nativeElement;
    this.backgroundEl = canvasEl;
    const context = canvasEl.getContext("2d");
    let logo = new Image();
    logo.src = emptyLogo;

    canvasEl.width = this.width;
    canvasEl.height = this.height;

    logo.onload = () => {
      const xOffset = (this.width - logo.width) / 2;
      const yOffset = (this.height - logo.height) / 2;
      context.drawImage(logo, xOffset, yOffset, logo.width, logo.height);
    };

    const progressHeight = this.percentage * (this.height / 100);
    const blankHeight = this.height - progressHeight;

    canvasEl.height = blankHeight;
  }

  loadImage() {
    const canvasEl: HTMLCanvasElement = this.canvas.nativeElement;
    this.ctx = this.canvas.nativeElement.getContext("2d");
    let border = new Image();
    let logo = new Image();

    canvasEl.width = this.width;
    canvasEl.height = this.height;

    border.src = logoBorder;
    logo.src = logoPainted;
    border.onload = () => {
      this.ctx.drawImage(border, 0, 0, this.width, this.height);
    };
    logo.onload = () => {
      const xOffset = (this.width - logo.width) / 2;
      const yOffset = (this.height - logo.height) / 2;
      this.ctx.drawImage(logo, xOffset, yOffset, logo.width, logo.height);
    };
  }
}
