import {
  Component,
  OnInit,
  Input,
  HostBinding,
  ElementRef,
  ViewChild,
} from "@angular/core";

@Component({
  selector: "app-button",
  templateUrl: "./button.component.html",
  styleUrls: ["./button.component.scss"],
})
export class ButtonComponent implements OnInit {
  constructor() {}

  @Input() secondary: boolean;

  @Input() secondaryFeedback: boolean;

  @Input() primary: boolean = true;

  @Input() feedback: boolean = false;

  @Input() disabled: boolean;

  @Input() size: string = 'medium';

  @Input() tertiary: boolean;

  @Input() ghost: boolean;

  @Input() danger: boolean;

  @Input() success: boolean;

  clicked = false;

  @HostBinding("class") @Input("class") classList: string = "__icone";
  @ViewChild('buttonContent') buttonContent:ElementRef;

  ngOnInit(): void {
    if (this.tertiary || this.ghost) this.primary = false;
  }
  
  ngAfterViewInit() {
    let buttonstring = this.buttonContent.nativeElement.innerHTML;
    let splitted = buttonstring.split("<app-icon", 2); 
    let removeSpacesLeftRight = splitted[0].trim();
    this.buttonContent.nativeElement.innerHTML = removeSpacesLeftRight + "<app-icon class='ml-3'" + splitted[1];
    // console.log(this.buttonContent.nativeElement.innerHTML);
  }

  clickEvent() {
    this.clicked = true;
    setTimeout(() => {
      this.clicked = false;
    }, 500);
  }
}
