import { Component, OnInit, EventEmitter, Output } from "@angular/core";

@Component({
  selector: "app-overflow-menu",
  templateUrl: "./overflow-menu.component.html",
  styleUrls: ["./overflow-menu.component.scss"],
})
export class OverflowMenuComponent implements OnInit {
  @Output() bookmarkClick = new EventEmitter<any>();
  @Output() printClick = new EventEmitter<any>();
  @Output() frameClick = new EventEmitter<any>();
  @Output() closeClick = new EventEmitter<any>();
  @Output() selected = new EventEmitter<any>();

  constructor() {}

  ngOnInit(): void {}

  clickOnBookmark(event) {
    this.bookmarkClick.emit(event);
    this.selected.emit("Bookmark");
  }

  clickOnPrint(event) {
    this.printClick.emit(event);
    this.selected.emit("Print");
  }

  clickOnFrame(event) {
    this.frameClick.emit(event);
    this.selected.emit("Frame");
  }

  clickOnClose(event) {
    this.closeClick.emit(event);
    this.selected.emit("Close");
  }
}
