import {
  Component,
  OnInit,
  ContentChild,
  Input,
  Output,
  EventEmitter,
} from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";

interface PanelItem {
  name: string;
  status: string;
  checked: boolean;
}
@Component({
  selector: "app-panel",
  templateUrl: "./panel.component.html",
  styleUrls: ["./panel.component.scss"],
})
export class PanelComponent implements OnInit {
  @Input() firstHeader: string = "Files";
  @Input() secondHeader: string = "Download Status";
  @Input() title: string = "Downloads";
  @Input() buttonName: string = "Download";
  @Input() panelItems: Array<PanelItem> = [];
  @Output()
  panelItemsChange: EventEmitter<PanelItem[]> = new EventEmitter<PanelItem[]>();
  @Output() buttonClick: EventEmitter<any> = new EventEmitter();

  constructor() {}
  ngOnInit() {}

  checkCheckboxes($event, index) {
    this.panelItems[index].checked = $event;
    this.panelItemsChange.emit(this.panelItems);
  }
}
