import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  OnChanges,
  SimpleChanges,
} from "@angular/core";

@Component({
  selector: "app-pagination",
  templateUrl: "./pagination.component.html",
  styleUrls: ["./pagination.component.scss"],
})
export class PaginationComponent implements OnInit, OnChanges {
  @Input() page: number = 0; // Pagina selecionada inicialmente
  @Input() numberOfItems: number = 0; // numero total de items
  @Input() itemsPerPage: number = 0; // numero total de items por pagina
  @Input() pageSizeOptions: any = [10, 25, 100];
  @Input() hidePageSize: boolean = true;
  @Input() maxSize: number = 5;
  collectionSize;
  @Output() pageChange = new EventEmitter<any>();
  @Output() pageSize = new EventEmitter<any>();

  constructor() {}
  ngOnChanges(changes: SimpleChanges): void {
    if(this.itemsPerPage > 0) {
      this.collectionSize = (this.numberOfItems * 10 ) / this.itemsPerPage;
    } else {
      this.collectionSize = this.numberOfItems * 10;
    }
  }
  ngOnInit(): void {
    if(this.itemsPerPage > 0) {
      this.collectionSize = (this.numberOfItems * 10 ) / this.itemsPerPage;
    } else {
      this.collectionSize = this.numberOfItems * 10;
    }
  }
}
