import { FormControl, NG_VALUE_ACCESSOR, ControlValueAccessor } from "@angular/forms";
import {
  Component,
  OnInit,
  Input,
  HostBinding,
  Output,
  EventEmitter,
  forwardRef,
  Injector, 
  ViewChild,
  ElementRef
} from "@angular/core";

@Component({
  selector: "app-textinput",
  templateUrl: "./textinput.component.html",
  styleUrls: ["./textinput.component.scss"],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TextinputComponent), // replace name as appropriate
      multi: true,
    },
  ],
})

/**
 * Componente TextInput
 * Last Modified 05/01/2021
 * @author Jefferson Cardoso
 * @email jcardoso@atech.com.br
 */

export class TextinputComponent
  implements OnInit, ControlValueAccessor {

  public onChange = (name: any) => {};
  public onTouched = () => {};

  hide = false;
  appearWarnIcon = false;
  @HostBinding("class") class = "";
  @Input() type: string = "";
  @Input() label: string = "Label";
  @Input() required: boolean = false;
  // Desabilitando o Campo
  @Input() disabled: boolean;
  //Quantidade maxima de letras
  @Input() maxLetters: number;
  @Output() input = new EventEmitter<string>();
  @Output() blur = new EventEmitter<any>();
  // Dados do formulrio reativo
  @Input() formControl = new FormControl("");
  @Input() maxlength: number = 255;
  @Input() minlength: number = 0;
  @Input() keyUpFn: (event: KeyboardEvent) => any  = (event: KeyboardEvent) => {}
  // Mensagem de erro
  // Exemplo de aplicao no componente
  // html
  //   <app-textinput
  //   placeholder="ivan@gmail.com"
    //   [formControl]="email"
  //   [errorMessage]="getErrorMessage()"
  //    ></app-textinput>
  // ts
  // getErrorMessage() {
  //   if (this.form.controls.email.hasError("required") && this.email.touched) {
  //     return "You must enter a value";
  //   }

  //   return this.form.controls.email.hasError("email") ? "Not a valid email" : "";
  // }
  @Input() errorMessage: string;
  // placeholder comum
  @Input() placeholder: string;

  @Input() mask?: string;

  @ViewChild('message') public elm: ElementRef;

  @HostBinding("class") textColor;
  @Input("class") @HostBinding("class") classList;

  selectedValue: any;

  // Constructor
  constructor( public inj: Injector ) {}


  ngOnInit() {
    this.formControl.valueChanges.subscribe((n) => this.verifyValidity());
    this.hide = this.type === "password" ? true : false;
    this.textColor = this.hide ? "password" : "text";
    // define as mascaras se houver
    // if (this.mask){
    //   this.createGetSetValue(this.elm.nativeElement, this.mask);
    // }
  }


  changeHide() {
    this.hide = !this.hide;
    if (this.hide) {
      this.textColor = "";
      this.textColor = "password";
    } else {
      this.textColor = "";
      this.textColor = "text";
    }
  }

  changeFocus(event) {
    if (event.type === "focus") {
      this.resetStyles();
      this.class = "focus";
      this.formControl.setErrors(null);
    } else {
      this.verifyValidity();
      this.blur.emit(event);
    }
  }

  verifyValidity() {
    this.onChange(this.formControl.value);
    if (this.formControl.invalid && this.formControl.touched) {
      this.setWarningStyle();
    } else {
      this.resetStyles();
    }
  }
  setWarningStyle() {
    this.appearWarnIcon = true;
    this.class = "warning";
    this.textColor = "error";
  }

  resetStyles() {
    this.appearWarnIcon = false;
    this.class = "";
    this.textColor = "text";
  }

  emit(event) {
    this.input.emit(event);
  }

  // ControlValueAccessor implementation
  validate: boolean = false;


  // this method sets the value programmatically
  writeValue(value: any) {
    this.formControl.setValue(value);
  }
  // upon UI element value changes, this method gets triggered
  registerOnChange(fn: any) {
    this.onChange = fn;
  }
  // upon touching the element, this method gets triggered
  registerOnTouched(fn: any) {
    this.onTouched = fn;
  }

subCharAt(str: string, c: string, pos: number): string {
    return str.substring(0, pos) + c;
}

createGetSetValue(inputElement: HTMLInputElement, mask: string) {
    const _self = this;

    const changeFn = function (this: HTMLInputElement) {

      const curVal = this.value;

      let newValue = mask;
      let lastIndexValid = 0;
      for (let x = 0; x < mask.length; x++) {
        if (x >= curVal.length) {
          newValue = _self.subCharAt(newValue, mask[x].replace(/[\@\#\$\*8]/g, '_'), x);
        } else {
          if ((mask[x].match(/[\@\*\$\#8]/g) && curVal[x].match(/[0-9a-zA-Z]/g))
            || (mask[x] === curVal[x] && !mask[x].match(/[\@\*\$\#_]/g))
          ) {
            newValue = _self.subCharAt(newValue, curVal[x], x);
            lastIndexValid = x + 1;
          } else {
            newValue = _self.subCharAt(newValue, mask[x].replace(/[\@\#\$\*8]/g, '_'), x)
          }

        }
      }
      this.dataset.lastIndexvalid = lastIndexValid.toString();
      this.value = newValue.toUpperCase();
      this.setSelectionRange(lastIndexValid, lastIndexValid);

    }.bind(inputElement)

    inputElement.oninput = changeFn;
    inputElement.onchange = changeFn;
    inputElement.onloadstart = changeFn;
    const fnFocus = function (this: HTMLInputElement) {
      changeFn();
      let c = this.dataset.lastIndexvalid;
      const iofu = this.value.indexOf('_');
      if (iofu >= 0) {
        c = iofu.toString();
      }

      const origLength = c ? parseInt(c) : 0;
      this.setSelectionRange(origLength, origLength);
    }.bind(inputElement);
    inputElement.onfocus = fnFocus;
    inputElement.onclick = fnFocus;
    inputElement.dataset.isMaskSet = 'true';
    inputElement.onblur = function (this: HTMLInputElement) {
      this.value = this.value.replace(/_/g, '');

    }.bind(inputElement);

    return inputElement;
  };

    public maskF(e: KeyboardEvent, mask: string | undefined, isUppercase = true): boolean {
    if (!mask) {
      return true;
    }
    const input = e.target as HTMLInputElement;
    if (!input.dataset.isMaskSet) {
      this.createGetSetValue(input, mask);
      input.dataset.isMaskSet = 'true';
    }
    let curVal = input.value ? input.value : '';
    if (!e.key || e.keyCode <= 47) {
      return true;
    }
    curVal = curVal.replace(/_.*$/g, '');
    if (curVal.length >= mask.length) {
      input.value = curVal.substring(0, mask.length);
      e.stopPropagation();
      return false;
    }
    const v = mask[curVal.length];

    switch (v) {
      case '8': // Octal
        if (!e.key.match(/[0-7]/)) {
          e.preventDefault();
          return false;
        }
        break;
      case '#':
        if (!e.key.match(/[0-9]/)) {
          e.preventDefault();
          return false;
        }
        break;
      case '$':
        if (!e.key.match(/[a-zA-Z]/)) {
          e.preventDefault();
          return false;
        }
        break;
      case '@':
        if (!e.key.match(/[a-zA-Z0-9]/)) {
          e.preventDefault();
          return false;
        }
        break;
      case '*':
        break;
      default:
        input.value = curVal + v;
        e.preventDefault();
        return false;

    }

    if (curVal.length + 1 < mask.length) {
      const nextVal = mask[curVal.length + 1];
      if (!nextVal.match(/[\@\$\#\*8]/)) {

      }
    }
    if (isUppercase) {
      input.dataset.rawValue = curVal.toUpperCase();

      input.value = curVal.toUpperCase();
    }

    return true;
  }
}
