import {
  Component,
  OnInit,
  ContentChild,
  Input,
  Output,
  ViewChild,
  EventEmitter,
  AfterContentInit,
  ViewChildren,
} from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";

export type SortDirection = "asc" | "desc" | "";
const rotate: { [key: string]: SortDirection } = {
  asc: "desc",
  desc: "",
  "": "asc",
};
const compare = (v1: string, v2: string) => (v1 < v2 ? -1 : v1 > v2 ? 1 : 0);

@Component({
  selector: "app-datatable",
  templateUrl: "./datatable.component.html",
  styleUrls: ["./datatable.component.scss"],
})
export class DatatableComponent implements OnInit, AfterContentInit {
  @ContentChild("atechTableHead")
  tableHead;
  @ContentChild("atechTableBody")
  tableBody;
  @ViewChildren("paginator") paginator;

  directions: SortDirection[] = [""];
  @Output() threeClick = new EventEmitter<any>();

  headers = [];
  sortableHeaders = [];
  lines = [];
  sortedLines = [];
  pageLines: Array<any> = [];
  pageSize: number = 10;
  numberOfPages: number;
  actualPage: number = 1;

  constructor() {}
  ngAfterContentInit(): void {
    this.fillHeader();
    this.fillTable();
    this.sortedLines = this.lines;
    this.pageLines = this.sortedLines.slice(0, this.pageSize);
    this.numberOfPages = this.sortedLines.length / this.pageSize;
  }

  fillHeader() {
    const headers = this.tableHead.nativeElement.children;
    for (let i = 0; i < headers.length; i++) {
      this.headers.push(headers[i].innerText.trim());
      this.directions.push("");
      if (headers[i].attributes.sortable) {
        this.sortableHeaders.push(true);
      } else {
        this.sortableHeaders.push(false);
      }
    }
  }

  fillTable() {
    const lines = this.tableBody.nativeElement.children;
    for (let i = 0; i < lines.length; i++) {
      let line = lines[i];
      let tableLine = [];
      for (let j = 0; j < line.cells.length; j++) {
        let cell = line.cells[j];
        tableLine.push(cell.innerText);
      }
      if (line.cells.length < this.headers.length) {
        tableLine.push("");
      }
      this.lines.push(tableLine);
    }
  }

  ngOnInit(): void {}

  rotate(column) {
    this.directions[column] = rotate[this.directions[column]];
    this.onSort(column, this.directions[column]);
  }

  onSort(column, direction) {
    // resetting other headers
    for (let i = 0; i < this.headers.length; i++) {
      if (i !== column) {
        this.directions[i] = "";
      }
    }

    // sorting countries
    if (direction === "") {
      this.sortedLines = this.lines;
    } else {
      this.sortedLines = [...this.sortedLines].sort((lineA, lineB) => {
        const res = compare(`${lineA[column]}`, `${lineB[column]}`);
        return direction === "asc" ? res : -res;
      });
    }
    this.changePage(this.actualPage);
  }

  changePageSize(event) {
    this.pageSize = event.pageSize;
    this.numberOfPages = this.sortedLines.length / this.pageSize;
    this.pageLines = this.sortedLines.slice(0, this.pageSize);
  }
  changePage(actualPage) {
    this.actualPage = actualPage;
    this.pageLines = [];
    const pageSize = this.pageSize;
    const length = this.lines.length;
    const start = (actualPage - 1) * pageSize;
    const end = start + pageSize;

    if (end > length) {
      this.pageLines = this.sortedLines.slice(start, length);
    } else {
      this.pageLines = this.sortedLines.slice(start, end);
    }
  }
}
