import {
  Component,
  OnInit,
  ViewContainerRef,
  ViewChild,
  ContentChild,
  ComponentRef,
  ComponentFactoryResolver,
  ComponentFactory,
  EventEmitter,
  Output,
  Input,
  AfterViewInit,
  ViewChildren,
  ContentChildren,
  ElementRef,
  AfterContentChecked,
  AfterViewChecked,
  AfterContentInit,
} from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";

@Component({
  selector: "app-stepper",
  templateUrl: "./stepper.component.html",
  styleUrls: ["./stepper.component.scss"],
})
export class StepperComponent implements OnInit, AfterViewInit {
  isLinear = false;
  firstFormGroup: FormGroup;
  @Input() selectedIndex: number = 0;
  @Output() sent: EventEmitter<any> = new EventEmitter();

  steps: Array<{ name: string; verticalSteps: HTMLCollection }> = [];
  @ContentChildren("horizontalStep")
  stepsList: any;

  constructor(private _formBuilder: FormBuilder) {}

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      firstCtrl: ["", Validators.required],
    });
  }

  ngAfterViewInit(): void {
    this.fillStepper();
  }

  fillStepper() {
    this.stepsList.forEach((element: ElementRef<any>) => {
      const name = element.nativeElement.attributes.name.value;
      const verticalSteps = element.nativeElement.children;
      this.steps.push({ name, verticalSteps });
    });
  }

  changeSelectedIndex(isDone, stepper) {
    if (isDone) {
      if (this.selectedIndex < this.steps.length - 1) this.selectedIndex += 1;
      else stepper.reset();
    }
  }
}
