import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ViewEncapsulation,
  ViewChild,
  ChangeDetectionStrategy,
} from "@angular/core";
import { MatSelect } from "@angular/material/select";

interface Food {
  value: string;
  viewValue: string;
}

@Component({
  selector: "app-select",
  templateUrl: "./select.component.html",
  styleUrls: ["./select.component.scss"],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SelectComponent implements OnInit {
  @ViewChild('selectElement') selectElement: MatSelect;
  selected: string;
  selecionado: string;
  item: 'checked';
  state: boolean = false;
  @Input() values: string[];
  @Input() placeholder: string;
  @Output() isSelected = new EventEmitter<string>();
  constructor() {}

  ngOnInit() {
    let element = getComputedStyle(document.body)
  }

  onChange() {
    this.isSelected.emit(this.selected);
  }

  alterState(e: boolean) {
    this.state = !e;
  }
  $thisSelected(nameSelected: string) {
    this.selected = nameSelected;
  }
}
