import {
  Component,
  OnInit,
  ContentChild,
  AfterViewInit,
  Input,
  Output,
  EventEmitter,
} from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";

@Component({
  selector: "app-vertical-stepper",
  templateUrl: "./vertical-stepper.component.html",
  styleUrls: ["./vertical-stepper.component.scss"],
})
export class VerticalStepperComponent implements OnInit, AfterViewInit {
  @Input() verticalStepsCollection: HTMLCollection;
  @Output() sent: EventEmitter<any> = new EventEmitter();
  @Output() done: EventEmitter<any> = new EventEmitter();
  isLinear = false;
  firstFormGroup: FormGroup;
  steps: Array<{ name: string; content: string }> = [];

  constructor(private _formBuilder: FormBuilder) {}
  ngAfterViewInit(): void {}

  ngOnInit() {
    this.fillSteps();

    this.firstFormGroup = this._formBuilder.group({
      firstCtrl: ["", Validators.required],
    });
  }

  fillSteps() {
    for (let index = 0; index < this.verticalStepsCollection.length; index++) {
      const element = this.verticalStepsCollection[index];
      const name = element.attributes.getNamedItem("name").value;
      const content = element.innerHTML;
      this.steps.push({ name, content });
    }
  }

  function(stepper) {
    stepper.reset();
  }

  enviar(name, isLast) {
    this.sent.emit(name);
    if (isLast) this.done.emit(true);
  }
}
