import {
  Component,
  OnInit,
  ContentChildren,
  AfterContentInit,
  Output,
  EventEmitter,
  Input,
} from "@angular/core";

@Component({
  selector: "atech-tab-group",
  templateUrl: "./tab.component.html",
  styleUrls: ["./tab.component.scss"],
})
export class TabComponent implements OnInit, AfterContentInit {
  @ContentChildren("atechTab") tabViews;
  @Input() selectedIndex = 0;
  @Output() selectedTabChange: EventEmitter<any> = new EventEmitter<any>();
  tabs = [];

  constructor() {}
  ngAfterContentInit(): void {
    this.fillTabs();
  }

  fillTabs() {
    this.tabViews._results.forEach((element, index) => {
      let tab = { label: "", content: "", icon: false, badge: 0, index };

      const label = element.nativeElement.children[0];
      const attr = label.attributes;

      tab.label = label.innerText;
      if (attr.icon) tab.icon = true;
      if (attr.badge) tab.badge = attr.badge.nodeValue;

      const content = element.nativeElement.children[1];
      tab.content = content.innerHTML;

      this.tabs.push(tab);
    });
  }

  ngOnInit(): void {}
}
